export default{
	data(){
		return {
		};
		
	},
	
	methods:{	
		register_submit:function(e){
			//逻辑
			var userName=e.detail.value.userName;
			var passWord=e.detail.value.passWord;
			var rePassWord=e.detail.value.rePassWord;
			
			if(userName=="" ||passWord=="" || rePassWord==""){
				uni.showToast({
					title:"请填写完整",
					icon:"none"
				})
			}
			else if(passWord!=rePassWord){
				uni.showToast({
					title:"密码不一致",
					icon:"none"
				});
			}
			else if(passWord==rePassWord){
				uni.request({
					url: 'https://getman.cn/mock/success',
					// url: 'https://getman.cn/mock/false', 
					success: (res) => {
						uni.hideLoading();
						if(res.data.success=="true"){
							uni.redirectTo({
								url:"../login/login",
								success:function(){
									uni.showToast({
										title:"注册成功"
									});
								}
							})
						}
						else{
							uni.showToast({
								title:"请求错误，请重试",
								icon:"none"
							})
						}
					}
				})
				
				uni.showLoading({
					title:"请求中"
				});
				
			}
		},
		
		toLogin:function(){
			uni.redirectTo({
				url:"../login/login"
			})
		}
		
	}
}
